import {
  Card,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label

} from 'reactstrap'
import React, { useState, useEffect, Fragment } from 'react'
import UiLoader from '@components/ui-loader'
import { useForm, Controller } from 'react-hook-form'
import { selectThemeColors } from '@utils'
import Select from 'react-select'
import classnames from 'classnames'
import { useSelector, useDispatch } from 'react-redux'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import { DragDrop } from '@uppy/react'
import './styles copy.scss'
import './styles.scss'
import Uppy from '@uppy/core'
import '@styles/react/libs/file-uploader/file-uploader.scss'
import {  updateAdData, getPrizeList } from '../../store/actions'
import { useParams } from 'react-router-dom'
import { getcricketSeries, getfootballSeries } from '../../../banneruploaed/store/action'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

const selectstatus = [
  { value: '1', label: 'Video' },
  { value: '2', label: 'Image' }
]

const sportscategoryOption = [
  { value: 'cricket', label: 'Cricket', sportstype: 'active' },
  { value: 'football', label: 'Football' }
]

const categoryOptions = [
  { value: 'challenge ', label: 'Challenge', active: 'active' },
  { value: 'challenge_info ', label: 'Challenge Info ', active: 'active' },
  { value: 'external_urls', label: 'External Urls', url: 'url' },
  { value: 'redeem', label: 'Redeem' },
  { value: 'redeem_info', label: 'Redeem Info' },
  { value: 'refer_friend', label: 'Refer Friend' },
  { value: 'wallet', label: 'Wallet' }
]

const VerticalForm = () => {

  const dispatch = useDispatch()
  const store = useSelector(state => state.ads.editData)
  const store1 = useSelector(state => state.ads)
  const store2 = useSelector(state => state.banner)
  //const [editBrdAd, setEditBrdAd] = useState({ name: store?.name, playing_time: store?.playing_time, max_count: store?.max_count, max_user_count: store?.max_user_count, expiry_date: store?.expiry_date, type: store?.type, cta: store?.cta, asset_url: store?.asset_url, redeem_code: store?.redeem_code })
  const [imageUpload, setImageUpload] = useState('')
  const [imageError] = useState(true)
  const [date_error, setDate_error] = useState(false)
  const [category, setCategory] = useState('')
  const params = useParams()
  const [checksportstype, Setchecksportstype] = useState(false)
  const [challange_active, setchallange_active] = useState(false)
  const [urltext_box, setUrltext_box] = useState(false)
  const [val, setVal] = useState(false)
  const [page, setPage] = useState(1)
  const [value] = useState(null)
  const [currentSporttype, setCurrentSporttype] = useState({
    value: '',
    label: 'Select Category'
  })
  const defaultValues = {
    reactSelect: null
  }
  const { register, errors, handleSubmit, control } = useForm({ defaultValues })

  const [editBrdAd, setEditBrdAd] = useState({})
  // const [updateBrdAd, setUpdateBrdAd] = useState({})

  useEffect(() => {
    dispatch(getPrizeList(page))
  }, [page])


  useEffect(() => {
    dispatch(getcricketSeries())
    dispatch(getfootballSeries())

  }, [])
  console.log(store1.editData)

  useEffect(() => {
    setEditBrdAd({ name: store?.name, playing_time: store?.playing_time, max_count: store?.max_count, max_user_count: store?.max_user_count, expiry_date: store?.expiry_date, type: store?.type, cta: store?.cta, asset_url: store?.asset_url })
  }, [store1.editData])

  const onSubmit = () => {
    console.log(editBrdAd)
    setDate_error(true)
    dispatch(updateAdData(params.id, editBrdAd))
  }

  const sportscategory = (val) => {
    setVal(false)
    setCurrentSporttype(val)
    console.log(val)
    if (val === 'cricket') {
      Setchecksportstype(true)
    } else {
      Setchecksportstype(false)
    }
  }

  const [currentCategory, setCurrentCategory] = useState({
    value: '',
    label: 'Select Category'
  })

  const handleCurrentCategory = (val) => {
    console.log(val)
    setEditBrdAd({ ...editBrdAd, cta: val.value })
    setCurrentCategory(val)

    if (val ? val.active : 'active') {

      setchallange_active(true)
    } else {
      setchallange_active(false)
    }

    if (val ? val.url : 'url') {

      setUrltext_box(true)
    } else {
      setUrltext_box(false)
    }

  }

  console.log(store2)
  const series = store2?.cricket_name?.map(function (ser) {

    return { value: ser.id, label: ser.series_name }
  })

  const [series_id, setSeries_id] = useState({
    value: '',
    label: 'Select Category'
  })

  const seriesCategory = (val) => {

    setSeries_id(val.value)
    setEditBrdAd({ ...editBrdAd, series_id: val.value })
  }


  const footballseries = store2?.football_name?.map(function (ser) {
    return { value: ser.id, label: ser.series_name }
  })

  const [footballseries_id, setFootballSeries_id] = useState({
    value: '',
    label: 'Select Category'
  })

  const footballSeriesCategory = (val) => {

    setFootballSeries_id(val)
    setEditBrdAd({ ...editBrdAd, series_id: val.value })
  }

  const editcategory = (e) => {
    console.log(e.value)
    setCategory(e)
    setEditBrdAd({ ...editBrdAd, type: e.value })
  }

  const sportscategorySelect = (value) => {
    setEditBrdAd({ ...editBrdAd, sports: value })
    sportscategory(value)
  }

  // const typecategory = (e) => {
  //   setEditBrdAd({ ...editBrdAd, type: e.value })
  // }

  const redeemCodeHandler = (e) => {
    setEditBrdAd({ ...editBrdAd, redeem_code: e.value })
  }


  const uppy = new Uppy({
    meta: { type: 'avatar' },
    restrictions: { maxNumberOfFiles: 1 },
    allowedFileTypes: ['image/*'],
    autoProceed: true
  })
  console.log(editBrdAd)
  uppy.on("file-added", function (file) {

    const reader = new FileReader()
    reader.readAsDataURL(file.data)
    reader.onloadend = function () {
      const base64data = reader.result
      const imge = base64data
      console.log(imge)
      const img = new Image()
      console.log(img)
      img.src = base64data
      setImageUpload(base64data)
      setEditBrdAd({ ...editBrdAd, asset_url: base64data })
    }
  })

  if (store1.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  console.log(urltext_box, currentCategory, footballseries_id, series_id)

  console.log(store)
  console.log(editBrdAd)

  // ************************************************************************************* //

  const options = []
  for (let i = 0; i < store1.prizeList?.data?.total; i++) {
    if (page === store1.prizeList?.data?.current_page) {
      options.push({
        value: `${store1.prizeList?.data?.data[i]?.prizes_share_code}`,
        label: `${store1.prizeList?.data?.data[i]?.name}`
      })
    }

  }
  const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
  const optionsPerPage = 10

  const loadOptions = async (search, prevOptions, page) => {
    await sleep(1000)
    if (page === store1.prizeList?.data?.current_page) {
      const nextData = store1.prizeList?.data?.data?.map((nextData) => { return { value: nextData.prizes_share_code, label: nextData.name } })
      const scrollLength = [...prevOptions, ...nextData]
      let filteredOptions
      if (!search) {
        filteredOptions = scrollLength.length > 10 ? scrollLength : options
      } else {
        const searchLower = search.toLowerCase()
        filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
        )
      }

      const hasMore = Math.ceil(store1.prizeList?.data?.total / optionsPerPage) > page
      const slicedOptions = filteredOptions.slice(
        (page - 1) * optionsPerPage,
        page * optionsPerPage
      )

      return {
        options: slicedOptions,
        hasMore
      }
    }
  }
  const defaultAdditional = {
    page: 1
  }

  const loadPageOptions = async (q, prevOptions, { page }) => {
    setPage(page)
    const { options, hasMore } = await loadOptions(q, prevOptions, page)
    return {
      options: [...options],
      hasMore,

      additional: {
        page: page + 1
      }
    }
  }

  // ************************************************************************************* //

  return (
    <Fragment>
      <Card>
        <CardHeader>
          <CardTitle tag="h4">Add AD</CardTitle>
        </CardHeader>
        <CardBody>
          <Form onSubmit={handleSubmit(onSubmit)}>
            <Row>
              <Col sm="12" >
                <FormGroup>
                  <Label for="nameVertical">Select Sports Type</Label>
                  <Controller
                    control={control}
                    render={({ field: { ...field } }) => (
                      <Select
                        {...field}
                        //isClearable={false}
                        theme={selectThemeColors}
                        //className="react-select"
                        value={store.sports === "cricket" ? sportscategoryOption[0] : sportscategoryOption[1]}
                        classNamePrefix="select"
                        options={sportscategoryOption}
                        className={classnames('react-select', { 'is-invalid': currentSporttype.value === "" && val })}
                        isClearable={true}
                        onChange={(e) => sportscategorySelect(e.value)}
                      />
                    )}
                    name='reactSelect'
                  />
                </FormGroup>
              </Col>
              <Col sm="12">
                <FormGroup>
                  <Label for="seriesName">Name</Label>
                  <Input
                    type="name"
                    name="Name"
                    id="Name"
                    innerRef={register({ required: true })}
                    invalid={errors?.Name && true}
                    defaultValue={store?.name}
                    value={editBrdAd?.name}
                    onChange={(e) => setEditBrdAd({ ...editBrdAd, name: e.target.value })}
                    placeholder="Enter Name "
                  />
                </FormGroup>
              </Col>
              <Col sm="12">
                <FormGroup>
                  <Label for='seriesApiId'>Frequency</Label>
                  <Input
                    type="number"
                    name="digit"
                    id="digit"
                    innerRef={register({ required: true })}
                    invalid={errors.digit && true}
                    defaultValue={store?.playing_time}
                    value={editBrdAd?.playing_time}
                    onChange={(e) => setEditBrdAd({ ...editBrdAd, playing_time: e.target.value })}
                    placeholder="Enter Frequency"
                  />
                </FormGroup>
              </Col>
              <Col sm="12">
                <FormGroup>
                  <Label for='digit'>Total AD Count</Label>
                  <Input
                    type="number"
                    name="digit"
                    id="digit"
                    innerRef={register({ required: true })}
                    invalid={errors.digit && true}
                    defaultValue={store?.playing_time}
                    value={editBrdAd?.max_count}
                    onChange={(e) => setEditBrdAd({ ...editBrdAd, max_count: e.target.value })}
                    placeholder="Enter total ad count"
                  />
                </FormGroup>
              </Col>
              <Col sm="12">
                <FormGroup>
                  <Label for='digit'>User View Count</Label>
                  <Input
                    type="number"
                    name="digit"
                    id="digit"
                    innerRef={register({ required: true })}
                    invalid={errors.digit && true}
                    defaultValue={store?.max_user_count}
                    value={editBrdAd?.max_user_count}
                    onChange={(e) => setEditBrdAd({ ...editBrdAd, max_user_count: e.target.value })}
                    placeholder="Enter view count per user"
                  />
                </FormGroup>
              </Col>
              <Col sm="12" >
                <FormGroup>
                  <Label for="date-time-picker">date Time</Label>
                  <Controller
                    control={control}
                    render={({ field: { ...field } }) => (
                      <Flatpickr
                        {...field}
                        placeholder='select expiry date'
                        defaultValue={store?.expiry_date}
                        value={editBrdAd?.expiry_date}
                        onChange={(selectedDates, dateStr) => {
                          setEditBrdAd({ ...editBrdAd, expiry_date: dateStr })
                          setDate_error(false)
                          const firstDate = selectedDates[0]
                          console.log({ firstDate, dateStr })
                        }}
                        options={{
                          dateFormat: "Y-m-d H:i:ss"
                        }}
                        id='range-picker'
                        className={classnames('form-control', { 'is-invalid': (editBrdAd.expiry_date === "" && date_error) || (editBrdAd.date_time === 'NaN-NaN-NaN' && date_error) })}
                      />
                    )}
                    name='reactSelect'
                  />
                </FormGroup>
              </Col>
              <Col sm="12" >
                <FormGroup>
                  <Label for="nameVertical">Type</Label>
                  <Controller
                    control={control}
                    render={({ field: { ...field } }) => (
                      <Select
                        {...field}
                        onChange={(e) => editcategory(e)}
                        // defaultValue={store?.type === 1 ? selectstatus[0] : selectstatus[1]}
                        value={editBrdAd.type === 1 ? selectstatus[0] : selectstatus[1]}
                        options={selectstatus}
                        theme={selectThemeColors}
                        classNamePrefix='select'
                        isClearable={false}
                      />
                    )}
                    name='reactSelect'
                  />
                </FormGroup>
              </Col>
              <Col sm="12" >
                <FormGroup>
                  <Label for="nameVertical">AD Redirecting</Label>
                  <Controller
                    control={control}
                    render={({ field: { ...field } }) => (
                      <Select
                        menuPortalTarget={document.querySelector('body')}
                        {...field}
                        onChange={(data) => handleCurrentCategory(data)}
                        defaultValue={''}
                        options={categoryOptions}
                        theme={selectThemeColors}
                        classNamePrefix='select'
                        isClearable={false}
                      />
                    )}
                    name='reactSelect'
                  />
                </FormGroup>
              </Col>
              {
                editBrdAd.cta === 'redeem_info' ?
                  <Col sm='12'>
                    <FormGroup>
                      <Label>Prize List</Label>
                      <AsyncPaginate
                        menuPortalTarget={document.querySelector('body')}
                        theme={selectThemeColors}
                        classNamePrefix='select'
                        additional={defaultAdditional}
                        value={value?.label}
                        loadOptions={loadPageOptions}
                        onChange={(e) => redeemCodeHandler(e)}
                      />
                    </FormGroup>
                  </Col> : ''
              }
              {
                category.label === 'Video' ?
                  <Col sm="12">
                    <FormGroup>
                      <Label className="form-label" for="post_url" >Video Url</Label>
                      <Input
                        type="text"
                        name="post_url"
                        id="post_url"
                        defaultValue={store?.asset_url}
                        value={editBrdAd.asset_url}
                        onChange={(e) => setEditBrdAd({ ...editBrdAd, asset_url: e.target.value })}
                        placeholder="Enter Video Url"
                      />
                    </FormGroup>
                  </Col> : ''
              }

              <Col>
                <FormGroup>
                  <Label className="form-label" for="post_url" >Screen re-driect</Label>
                  <Input
                    type="text"
                    name="post_url"
                    id="post_url"
                    defaultValue={store?.cta.url}
                    value={editBrdAd?.cta?.url}
                    onChange={(e) => setEditBrdAd({ ...editBrdAd, cta: e.target.value })}
                    placeholder="Enter Redirecting Screen"
                  />
                </FormGroup>
              </Col>
            </Row>
            {challange_active ?

              <Col sm='12'>
                {checksportstype ? <FormGroup  >
                  <Label>Cricket Series</Label>
                  <Select
                    menuPortalTarget={document.querySelector('body')}
                    isClearable={false}
                    theme={selectThemeColors}
                    className="react-select"
                    classNamePrefix="select"
                    options={series}
                    value={series.label}
                    onChange={(data) => {
                      seriesCategory(data)
                    }}

                  />

                </FormGroup> : <FormGroup  >
                  <Label>Football Series</Label>
                  <Select
                    menuPortalTarget={document.querySelector('body')}
                    isClearable={false}
                    theme={selectThemeColors}
                    className="react-select"
                    classNamePrefix="select"
                    options={footballseries}
                    value={footballseries.label}
                    onChange={(data) => {
                      footballSeriesCategory(data)
                    }}

                  />

                </FormGroup>}

              </Col>

              : ''
            }
            {category.label === 'Image' ?
              <Row className="wrapper-add" >
                <Col className="add-post-edit">
                  <div className={`mt-4 ${imageError ? 'img-error-post' : ''}`}>
                    <DragDrop uppy={uppy}
                      locale={{
                        strings: {
                          dropHereOr: 'Drop file or %{browse}',
                          browse: 'browse'
                        }
                      }}
                    />
                    {imageUpload && (
                      <div className="uploaded-files mt-2">
                        <img src={imageUpload} className="post-preview" />
                      </div>
                    )}
                  </div>
                </Col>
              </Row> : ''}
            <Row className='mt-1'>
              <Col sm="12">
                <FormGroup className="d-flex mb-0">
                  <Button.Ripple
                    className="mr-1"
                    color="primary"
                    type="submit"
                  >
                    Submit
                  </Button.Ripple>
                  {/* <Button.Ripple
                    outline
                    color="secondary"
                    type="reset"
                    onClick={(e) => onclear(e)}
                  >
                    Reset
                  </Button.Ripple> */}
                </FormGroup>
              </Col>
            </Row>
          </Form>
        </CardBody>
      </Card>
    </Fragment>
  )
}
export default VerticalForm

